import React ,{useState}from 'react'
import axios from 'axios'
import CodeEditor from '@uiw/react-textarea-code-editor';
import "./styles.css"
// import Editor  from 'react-simple-code-editor';
// import { highlight, languages } from 'prismjs/components/prism-core';
// import 'prismjs/components/prism-clike';
// import 'prismjs/components/prism-python';
// import 'prismjs/themes/prism.css';
function EditorC() {
  const [bstatus,setBstatus] = useState(true);
  const [code, setCode] = useState(`#write your code`);
  const [input,setInput] = useState("");
  const [output,setOutput] = useState("");
  const [isWait,setIsWait] = useState(true);
const asyPromise=(cd,ip)=>{
  
return new Promise((resolve,reject)=>{
  setTimeout(()=>{reject(new Error("Call Took Much Time"))},100000);
  var data = {
    code: cd,
    input: ip,
  }
  // console.log(data)
  axios({
    method: "post",
    url: "https://sid-rce.centralindia.cloudapp.azure.com:5000/compile",
 
  data : data
  }).then(o=>{setBstatus(true);setOutput(o.data);resolve(o.data)}).catch(e=>{reject(new Error("code has some errors"));setBstatus(true);});

})
}



  const execute = (t)=>{
    if(bstatus==false) return ;
    if(isWait == false) {
      setOutput("Too soon! please wait for some more time before next execution")
      return ;
    }
    setIsWait(false);
    setTimeout(()=>{
setIsWait(true);
    },8000)
    setBstatus(false);
    setOutput("PleaseWait...")
    asyPromise(code,input).then((e)=>{
// console.log(e)
setOutput(e);
    }).catch((e)=>{
      setOutput("Execution TimeOut Error");
      // console.log(e)
    });
  }
    // var data = {
    //   code: code,
    //   input: input,
    // }
    // console.log(data)
    // axios({
    //   method: "post",
    //   url: "http://localhost:5000/compile",
   
    // data : data
    // }).then(o=>setOutput(o.data)).catch(e=>console.log(e));
  
    return (
    <div style={{"backgroundColor":"#ccffd4","paddingBottom":"140px"}}><h1>Python IDE Online</h1>
  

<CodeEditor
      value={code}
      language="python"
      // placeholder="Please enter JS code."
      onChange={(e)=>setCode(e.target.value)}
      padding={15}
      style={{
        fontSize: 15,
        backgroundColor: "#f5f5f5",
        fontFamily: '',
      }}
    />
<textarea placeholder='input' value = {input} onChange={(e)=>{setInput(e.target.value)}}></textarea>
<br></br>
<button className={bstatus?"btn enabled":"btn disabled"} onClick = {()=>execute(3000)}>{bstatus?<>Execute</>:<>Wait</>}</button>
<div className ="output">
  <h4 style = {{"textAlign":"left"}}>
Output:<br></br>
<pre >{output}</pre>
</h4>



</div>


    </div>
    
  )
}

export default EditorC